import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    container: {

    },
    form: {

    },
    fontCont: {
        display: "flex",
        alignItems: 'center'
    },
    font: {
        textTransform: "uppercase",
        fontSize: 15,
        fontWeight: 600
    },
    dropDown: {
        width: "calc(100% - 90px)",
        "& div": {
            fontSize: 17,
        },
        '& .MuiOutlinedInput-inputMarginDense': {
            color: '#3d4853',
            lineHeight: 1.6,
            padding: ' 12px 16px',
            fontWeight: 800,
            fontSize: '1.5rem',
        },
        '& .MuiFormHelperText-root.Mui-error': {
            fontSize: '13px'
        }
    },
    styleTxtCot: {
        display: "flex",
        alignItems: 'center',
        justifyContent: "space-around",
        "& p": {
            marginBottom: 'unset'
        },
        margin: "0px 5px"
    },
    styleTxt: {
        width: "30px !important",
        height: "45px !important",
        display: "flex",
        justifyContent: 'center',
        alignItems: "center",
        borderRadius: "5px",
        "&:hover": {
            backgroundColor: "#f1f4f8",
            cursor: 'pointer',
            border: "1px #ccc solid "
        },
    },
    colorInputModal: {

    },
    colorModalContainer: {
        width: 35,
        height: 35,
        borderRadius: "100%",
        border: "1px solid gray",
        backgroundColor: "#ccc",
        position: "relative",
        "&:hover": {
            backgroundColor: "#ececec",
            cursor: "pointer"
        },

    },
    colorModal: {
        position: "absolute",
        backgroundColor: "white",
        top: "50%",
        left: -200,
        width: 210,
        height: "fit-content",
        flexWrap: "wrap",
        // here transition didn't work
        // transition: "all 1s ease-out",
        borderRadius: 10,
        border: "1px solid #eee",
        boxShadow: "2px 2px 14px black",
        zIndex: 999
    },
    colorItem: {
        borderRadius: 30,
        width: 30,
        height: 30,
        margin: 5,
        border: '1px solid gray',
        "&:hover": {
            opacity: .5,
        }
    },
    colorMenu: {
        padding: 10
    },
    colorMenuHeader: {
        marginBottom: '10px',
        fontWeight: 600,
    },
    spacingCont: {
        display: 'flex'
    },
    sliderRightTxt: {
        width: 50,
        border: "2px solid #ccc",
        borderRadius: 7,
        marginLeft: 20,
        fontWeight: 600,
        fontSize: 16,
        textAlign: 'center',
        "&:focus": {
            border: "2px solid black"
        }
    },
    sliderLabel: {
        marginBottom: 'unset',
        marginTop: 20,
        fontSize: 15,
        textTransform: "uppercase"
    },
    backFrontCont: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
    },
    backFront: {
        display: 'flex',
        marginTop: 10,
        width: 50,
        height: 50,
        padding: 4,
        flexDirection: 'column',
        justifyContent: 'space-between',
        border: "1px solid transparent",
        borderRadius: 3,
        alignItems: 'center',
        "& p": {
            marginBottom: "unset",
            lineHeight: 1
        },
        "&:hover": {
            border: "1px solid black",
            cursor: 'pointer'
        }
    }
}))