import { MenuItem } from '@material-ui/core';
import { Delete, FlipToBack, FlipToFront, Redo, SwapHoriz, SwapVert, Undo } from '@material-ui/icons';
import { Field, Form, Formik } from 'formik';
import { TextField } from 'formik-material-ui';
import React, { useState } from 'react';
import { BonfireSlider } from './components';
import { useStyles } from './txtStuffs.style';

interface PropsType {
    applyFont: (e) => void;
    styleText: (txt) => void;
    textColor: (e) => void;
    spacing: (value) => void;
    rotate: (value) => void;
    size: (value) => void;
    moveLayer: (value) => void;
    flip: (value) => void;
    _delete: () => void;
    undo: () => void;
    redo: () => void;
    isTxt: boolean;
}

function isNumeric(n) {
    return !isNaN(parseFloat(n)) && isFinite(n);
}

export const TxtStuffs: React.FC<PropsType> = ({ size, isTxt, applyFont, styleText, textColor, spacing, rotate, moveLayer, flip, _delete, undo, redo }) => {
    const c = useStyles()
    const [openColorModal, setOpenColorModal] = useState(true)

    const colors = [
        "black",
        "aqua",
        "red",
        "yellow",
        "pink",
        "teal",
        "#aef",
        "thistle",
        "tomato",
        "coral",
        "chocolate",
        "azure",
        "aqua"
    ]

    var modal = document.getElementById('colorModal');
    window.onclick = (event) => {
        if (event.target != modal) {
            setOpenColorModal(false)
        }
    }

    return (
        <>
            <div style={{ width: "600px" }}>
                <Formik
                    initialValues={{
                        fontFamily: "Arial",
                        color: "#fff",
                        spacing: 20,
                        rotate: 0,
                        size: 37
                    }}
                    onSubmit={(a) => { console.log(a) }}
                >
                    {({ setFieldValue, values }): React.ReactNode => {
                        return (
                            <Form className={c.form}>
                                { isTxt &&
                                    <div className={c.fontCont}>
                                        <Field
                                            name="fontFamily"
                                            placeholder=""
                                            component={TextField}
                                            select
                                            variant="outlined"
                                            className={c.dropDown}
                                            onChange={(a) => {
                                                applyFont(a.target.value)
                                                setFieldValue("fontFamily", a.target.value)
                                            }}
                                        >
                                            <MenuItem style={{ fontSize: "17px" }} value="Arial" >Arial</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Arial Black" >Arial Black</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Calibri">Calibri</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Stencil">Stencil</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Garamond">Garamond</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Verdana">Verdana</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Georgia">Georgia</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Comic Sans MS">Comic Sans MS</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Trebuchet MS">Trebuchet MS</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value="Impact">Impact</MenuItem>
                                        </Field>
                                        <div className={c.styleTxtCot}>
                                            <p className={c.styleTxt} onClick={() => styleText('Bold')}>
                                                <strong>B</strong>
                                            </p>
                                            <p className={c.styleTxt} onClick={() => styleText('Italics')}>
                                                <strong>I</strong>
                                            </p>
                                            <p className={c.styleTxt} onClick={() => styleText('Underline')}>
                                                <strong>U</strong>
                                            </p>
                                        </div>
                                        <div
                                            id="colorModal"
                                            className={c.colorModalContainer}
                                            onClick={() => setOpenColorModal(!openColorModal)}
                                        >
                                            <div className={c.colorModal} style={{ display: openColorModal ? "flex" : "none" }}>
                                                {
                                                    colors.map(color => (
                                                        <div
                                                            onClick={() => textColor(color)}
                                                            className={c.colorItem}
                                                            style={{ backgroundColor: color }}
                                                        ></div>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    </div>
                                }

                                {
                                    isTxt &&
                                    <div>
                                        <p className={c.sliderLabel}>SPACING</p>
                                        <div className={c.spacingCont}>
                                            <BonfireSlider
                                                name="spacing"
                                                id="spacing"
                                                min={-20}
                                                max={180}
                                                value={values.spacing}
                                                handleChange={(_, newValue) => {
                                                    if (newValue > 180) newValue = 180
                                                    if (newValue < -20) newValue = -20
                                                    spacing(newValue)
                                                    setFieldValue("spacing", newValue)
                                                }}
                                            />
                                            <input onChange={(e) => {
                                                let value = e.target.value;
                                                if (isNumeric(value)) {
                                                    if (parseInt(value) > 180) {
                                                        value = '180';
                                                    }
                                                    if (parseInt(value) < -20) {
                                                        value = '-20'
                                                    }
                                                    spacing(value)
                                                    setFieldValue("spacing", value)
                                                }
                                            }}
                                                value={values.spacing} className={c.sliderRightTxt} type="text" name="spacing" id="spacing" />
                                        </div>
                                    </div>
                                }
                                <div>
                                    <p className={c.sliderLabel}>rotate</p>
                                    <div className={c.spacingCont}>
                                        <BonfireSlider
                                            name="rotate"
                                            id="rotate"
                                            min={-180}
                                            max={180}
                                            value={values.rotate}
                                            handleChange={(_, newValue) => {
                                                rotate(newValue)
                                                setFieldValue("rotate", newValue)
                                            }}
                                        />
                                        <input onChange={(e) => {
                                            let value = e.target.value;
                                            if (isNumeric(value)) {
                                                if (parseInt(value) > 180) {
                                                    value = '180';
                                                }
                                                if (parseInt(value) < -180) {
                                                    value = '-180'
                                                }
                                                rotate(value)
                                                setFieldValue("rotate", value)
                                            }
                                        }} value={values.rotate} className={c.sliderRightTxt} type="text" name="rotate" id="rotate" />
                                    </div>
                                </div>
                                {
                                    <div>
                                        <p className={c.sliderLabel}>SIZE</p>
                                        <div className={c.spacingCont}>
                                            <BonfireSlider
                                                name="size"
                                                id="size"
                                                min={0}
                                                max={300}
                                                value={values.size}
                                                handleChange={(_, newValue) => {
                                                    size(newValue)
                                                    setFieldValue("size", newValue)
                                                }}
                                            />
                                            <input onChange={(e) => {
                                                let value = e.target.value;
                                                if (isNumeric(value)) {
                                                    if (parseInt(value) > 300) {
                                                        value = '300';
                                                    }
                                                    if (parseInt(value) < 0) {
                                                        value = '0'
                                                    }
                                                    size(value)
                                                    setFieldValue("size", value)
                                                }
                                            }} value={values.size} className={c.sliderRightTxt} type="text" name="size" id="size" />
                                        </div>
                                    </div>
                                }
                                <div>
                                    <p className={c.sliderLabel}>actions</p>
                                    <div className={c.backFrontCont}>
                                        <div className={c.backFront} style={{ marginRight: 10 }} onClick={() => moveLayer("DOWN")}>
                                            <FlipToBack fontSize="large" />
                                            <p>Back</p>
                                        </div>
                                        <div className={c.backFront} onClick={() => moveLayer("UP")}>
                                            <FlipToFront fontSize="large" />
                                            <p>Front</p>
                                        </div>
                                        <div className={c.backFront} style={{ marginRight: 10, width: "80px" }} onClick={() => flip("X")}>
                                            <SwapHoriz fontSize="large" />
                                            <p>Horizontal</p>
                                        </div>
                                        <div className={c.backFront} style={{ width: 60, marginRight: 10 }} onClick={() => flip("Y")}>
                                            <SwapVert fontSize='large' />
                                            <p>Vertical</p>
                                        </div>
                                        <div className={c.backFront} style={{ width: 60 }} onClick={() => _delete()}>
                                            <Delete fontSize='large' color="error" />
                                            <p>Delete</p>
                                        </div>
                                        <div className={c.backFront} style={{ width: 60 }} onClick={() => undo()}>
                                            <Undo fontSize='large' />
                                            <p>Undo</p>
                                        </div>
                                        <div className={c.backFront} style={{ width: 60 }} onClick={() => redo()}>
                                            <Redo fontSize='large' />
                                            <p>Redo</p>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        )
                    }}
                </Formik>
            </div>
        </>
    )
}