import React, { useState } from 'react';
import { useStyles } from './addText.style';

interface PropsType {
    onClick: (txt) => void;
    onChange: (e) => void;
    value: string;
}

export const AddText: React.FC<PropsType> = ({ onClick, onChange, value }) => {
    const c = useStyles()
    const [txt, setTxt] = useState("")

    return (
        <>
            <div className={c.container}>
                <div className={c.textAreaContainer}>
                    <div className={c.txtCont}>
                        <textarea onChange={(e) => {
                            setTxt(e.target.value)
                            onChange(e)
                        }}
                            value={value} placeholder="Your text" className={`form-control ${c.textArea}`} name="addTxt" id="addTxt" cols={30} rows={7}></textarea>
                    </div>
                    <button className={`btn-primary btn-sm ${c.addTxtBtn} ${txt === "" ? c.addTxtBtnNoHover : null}`} disabled={txt === ""} style={{ opacity: txt === "" ? ".6" : 1 }} onClick={() => {
                        onClick(txt)
                    }}>
                        Add text
                    </button>
                </div>
            </div>
        </>
    )
}