import React, { Component } from 'react';
import DropNCrop from '@synapsestudios/react-drop-n-crop';

import '@synapsestudios/react-drop-n-crop/lib/react-drop-n-crop.min.css';
import "./crop.css"

class Crop extends Component {
    constructor(props) {
        super(props);
        this.state = {
            result: null,
            filename: null,
            filetype: null,
            src: null,
            error: null,
            cropClicked: false
        };
    }

    onChange = value => {
        this.setState(value)
    }

    render() {
        return (
            <>
                <DropNCrop className="root" onChange={this.onChange} value={this.state} />
                {
                    this.state.src &&
                    <button
                        onClick={() => {
                            this.props.onClick(this.state.result)
                            this.setState({ src: null })
                        }} style={{ marginTop: 20 }} className="btn-primary btn-sm">Crop the image</button>
                }
            </>
        )
    }
}

export default Crop;