import { Paper } from '@material-ui/core';
import React from 'react';
import { useStyles } from './addProduct.style';
import Close from "@material-ui/icons/Close";

interface Props {
    onClick: () => void;
    style: object;
}

export const AddProduct: React.FC<Props> = ({ onClick, style }) => {

    const c = useStyles()
    return (
        <Paper className={c.container} onClick={onClick} style={style}>
            <p className={c.addProduct}>
                + Add Products
            </p>
            <Close />
        </Paper>
    )
}