import { Slider, withStyles } from '@material-ui/core';
import React from 'react';

type PropsType = {
    value: number | number[];
    handleChange: (e, newValue) => void;
    min?: number;
    max?: number;
    name: string;
    id: string;
}

export const BonfireSlider: React.FC<PropsType> = ({ value, handleChange, min, max, name, id }) => {
    return (
        <>
            <CustomSlider
                name={name}
                id={id}
                value={value}
                min={min}
                max={max}
                onChange={(e, newValue) => handleChange(e, newValue)}
                aria-labelledby="continuous-slider" />
        </>
    )
}

const CustomSlider = withStyles({
    root: {
        color: "#6f8eff",
        height: 8,
        padding: "13px 0",
    },
    track: {
        height: 8,
        borderRadius: 8,
        backgroundColor: "transparent"
    },
    thumb: {
        height: 25,
        width: 25,
        backgroundColor: `#35CF94`,
        borderRadius: "100%",
        border: "3px solid white",
        marginTop: "-8.5px",
        marginLeft: -11,
        transition: "border 100ms",
        "&:hover": {
            border: "3px solid #35CF94",
            boxShadow: "none",
        },
        color: "#fff",
    },
    rail: {
        height: 8,
        borderRadius: 16,
        backgroundColor: "#ccc"
    }
})(Slider);
