import { IconButton, Paper } from '@material-ui/core'
import React, { useState } from 'react'
import { useStyles } from './productModal.style'
import shirt from "assets/icons/shirt.png";
import Done from "@material-ui/icons/Done"
import Exit from "@material-ui/icons/Close";
import Plus from "@material-ui/icons/Add";

interface PropsType {
    style: object;
}

export const ProductModal: React.FC<PropsType> = ({ style }) => {
    const [shirtColor, setShirtColor] = useState(0)


    const c = useStyles()
    return (
        <Paper className={c.container} elevation={8} style={style}>
            <div className={c.imgCont}>
                <img src={shirt} />
                <div className={c.shirtName}>
                    <p>Premium Unisex Tee</p>
                    <div className={c.colorsContainer}>
                        <div onClick={() => setShirtColor(0)} style={{ backgroundColor: "#ccc", border: "1px solid #ccc" }} className={c.color}>
                            {
                                shirtColor === 0 && <Done />
                            }
                        </div>
                        <div onClick={() => setShirtColor(1)} style={{ backgroundColor: "red", border: "1px solid #ccc" }} className={c.color}>
                            {
                                shirtColor === 1 && <Done />
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div className={c.rightCont}>
                <IconButton>
                    <Exit className={c.closeIcon} />
                </IconButton>
                <div className={c.rightBottom}>
                    <Plus className={c.plus} />
                    <div className={c.circleCont}>
                        <div style={{ backgroundColor: "orange" }} className={c.circle}></div>
                        <div style={{ backgroundColor: "yellow" }} className={c.circle}></div>
                        <div style={{ backgroundColor: "teal" }} className={c.circle}></div>
                        <div style={{ backgroundColor: "aqua" }} className={c.circle}></div>
                    </div>
                </div>
            </div>
        </Paper>
    )
}