import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    container: {
        width: "100%",
        "& div": {
            padding: "20px",
        },
        "& .MuiDialog-paper": {
            borderRadius: 20,
        }
    },
    content: {
        display: 'flex',
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "space-between",
    },
    circle: {
        width: 50,
        height: 50,
        backgroundColor: "red",
        borderRadius: "100% !important",
        marginBottom: 20
    },
    title: {
        textTransform: "uppercase",
        fontSize: 30,
        fontWeight: 500
    },
    subtitle: {
        textTransform: "capitalize"
    },
}))