import { Dialog } from '@material-ui/core';
import React from 'react';
import { useStyles } from './savingModal.style';

interface PropsTypes {
    open: boolean;
    handleClose: () => void;
}

export const SavingModal: React.FC<PropsTypes> = ({ open, handleClose }) => {
    const c = useStyles()
    return (
        <Dialog open={open} onClose={handleClose} className={c.container}>
            <div className={c.content}>
                <div className={c.circle}></div>
                <p className={c.title}>SAVING YOUR DESIGN</p>
                <p className={c.subtitle}>We'll make sure the artwork looks perfect before printing.</p>
            </div>
        </Dialog>
    )
}