import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    container: {
        display: 'flex',
        justifyContent: "space-between",
        width: 550
    },
    imgCont: {
        padding: "20px",
        display: "flex",
        "& img": {
            width: 100,
            userSelect: "none",
            pointerEvents: "none"
        }
    },
    shirtName: {
        marginLeft: 20
    },
    closeIcon: {
        color: "#aaa",
        fontSize: 30,
        fontWeight: 600
    },
    rightCont: {
        padding: "5px 10px",
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-end",
        "& .MuiIconButton-root": {
            padding: 5
        },
    },
    rightBottom: {
        display: 'flex',
        marginTop: 25
    },
    plus: {
        fontSize: 30,
        fontWeight: 900,
    },
    circleCont: {
        display: "flex",
        justifyContent: 'space-between',
        width: 35,
        flexWrap: "wrap",
        height: 40,
        alignItems: "space-between"
    },
    circle: {
        width: 15,
        height: 15,
        borderRadius: "100%"
    },
    // color circle
    colorsContainer: {
        display: "flex",
        marginBottom: 20,
        "&>div": {
            "&:hover": {
                cursor: "pointer",
                border: "1px solid #ccc"
            }
        }
    },
    color: {
        width: 30,
        height: 30,
        borderRadius: "100%",
        marginRight: 20,
        display: "flex",
        justifyContent: 'center',
        alignItems: 'center'
    },

}))