import { Typography } from "@material-ui/core"
import { Done } from "@material-ui/icons"
import { ReactComponent as Pen } from 'assets/icons/pen.svg'
import imgBigBack from 'assets/icons/shirt.png'
import avatar from 'assets/icons/singleProduct/avatar.png'
import imgBack from 'assets/icons/singleProduct/shirtBack.png'
import { BonfireField } from "components/BonfireField/BonfireField"
import { Form, Formik } from 'formik'
import React, { useState } from "react"
import { useStyles } from "./firstPayment.style"


export const FirstPayment = () => {
    const [isFront, setIsFront] = useState(true)
    const [shirtColor, setShirtColor] = useState(0)

    const c = useStyles()
    return (
        <>
            <div className={c.leftSection}>
                <div className={c.smallImgContainer}>
                    <img onClick={() => setIsFront(true)} style={{ border: isFront ? "1px solid #ccc" : "unset", marginBottom: 10 }} src={localStorage.getItem('img')?.toString()} />
                    <img onClick={() => setIsFront(false)} style={{ border: isFront ? "unset" : "1px solid #ccc" }} src={imgBack} />
                </div>
                <div className={c.imgContainer}>
                    <img
                        src={
                            isFront
                                ?
                                localStorage.getItem('img')?.toString()
                                :
                                imgBigBack
                        } />
                </div>
            </div>
            {/* right part */}
            <div className={c.rightSection}>
                <Formik
                    initialValues={{
                        fontFamily: "Arial",
                        color: "#fff",
                        spacing: 20,
                        rotate: 0
                    }}
                    onSubmit={(a) => { console.log(a) }}
                >
                    {({ }): React.ReactNode => {
                        return (
                            <Form>
                                <BonfireField
                                    className={c.title}
                                    name="title"
                                    placeholder="Click to add title"
                                    inputIcon={<Pen />}
                                />
                                <BonfireField
                                    className={c.description}
                                    name="description"
                                    placeholder="Describe your campaign in one, attention-grabbing sentence"
                                    inputIcon={<Pen />}
                                />

                                <div className={c.avatarContainer}>
                                    <img className={c.avatar} src={avatar} />
                                    <Typography>by Kanan Mikayilov</Typography>
                                </div>
                                <Typography className={c.colorTxt}>COLOR</Typography>
                                <div className={c.colorsContainer}>
                                    <div onClick={() => setShirtColor(0)} style={{ backgroundColor: "white", border: "1px solid #ccc" }} className={c.color}>
                                        {
                                            shirtColor === 0 && <Done />
                                        }
                                    </div>
                                    <div onClick={() => setShirtColor(1)} style={{ backgroundColor: "yellow" }} className={c.color}>
                                        {
                                            shirtColor === 1 && <Done />
                                        }
                                    </div>
                                    <div onClick={() => setShirtColor(2)} style={{ backgroundColor: "blue" }} className={c.color}>
                                        {
                                            shirtColor === 2 && <Done />
                                        }
                                    </div>
                                    <div onClick={() => setShirtColor(3)} style={{ backgroundColor: "green" }} className={c.color}>
                                        {
                                            shirtColor === 3 && <Done />
                                        }
                                    </div>
                                </div>
                                <Typography className={c.colorTxt}>STYLE</Typography>
                                <Typography style={{ fontSize: 17 }}>Premium Unisex Tee – $24.99 USD</Typography>

                                <button className={`btn-primary btn-sm ${c.btn}`}
                                    onClick={() => { }} >
                                    ADD TO CART
                                </button>
                                {/* cateogory */}
                                <div style={{ marginTop: 20, marginBottom: 10 }}>
                                    <Typography style={{ fontSize: 17 }}>About this campaign</Typography>
                                    <BonfireField
                                        className={c.description}
                                        name="category"
                                        placeholder="Add a short heading"
                                        inputIcon={<Pen />}
                                    />
                                </div>
                            </Form>
                        )
                    }}
                </Formik>
            </div>
        </>
    )
}