import React, { Component } from 'react';
import { fabric } from "fabric";
import shirt from './shirt.png'
import "./canvas.css"


class Canvas extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.canvas = new fabric.Canvas('canvas', {
            preserveObjectStacking: true
        });
        this.parentCanvas = new fabric.Canvas('parentCanvas', {
            selectable: false,
            evented: false
        });

        const ctx = this.canvas.getContext('2d');
        const parentCtx = this.parentCanvas.getContext('2d');

        fabric.Object.prototype.objectCaching = false;
        this.props.canvas(this.canvas);
        let img = new Image();
        img.src = shirt;
        img.onload = () => {
            parentCtx.drawImage(img, 0, 0, this.parentCanvas.width, this.parentCanvas.height);
        }
    }
    render() {
        return (
            <>
                <span className="canvasContainer">
                    <canvas id="parentCanvas" width="600px" height="600px" />
                    <canvas style={{ position: "absolute" }} id="canvas" width="250" height="400" />
                </span>
            </>
        );
    }
}

export default Canvas;