import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
    pageContainer: {
        backgroundColor: "white",
        padding: `40px ${theme.spacing(5)}px`,
        display: "flex"
    },
    leftPart: {
        width: "100%"
    },
    table: {
        overflowY: "scroll",
        maxHeight: 300
    },
    estimateCont: {
        display: "flex",
        alignItems: "center",
        marginBottom: 20,
        marginTop: 20
    },
    square: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        border: `2px solid  ${theme.palette.secondary.main}`,
        width: 35,
        height: 35
    },
    title: {
        fontSize: 23,
        fontWeight: 600,
        marginLeft: 10,
        letterSpacing: 1
    },
    subtitle: {
        fontSize: 16,
        marginBottom: 30
    },
    paper: {
        width: "fit-content",
        padding: 15,
        border: 0,
        borderRadius: 10,
        minWidth: 130,
        marginBottom: 20,
    },
    sectionContainer: {
        padding: "20px 20px 10px 0px"
    },
    draftContainer: {
        display: "flex",
        alignItems: "flex-start",
        justifyContent: 'space-between',
        width: 140,
        "&>p": {
            margin: 0,
            fontSize: 16
        },
    },
    btn: {
        padding: "10px 20px"
    },
    shirtImg: {
        width: 80,
        userSelect: "none",
        userDrag: "none"
    },
    tooltipCont: {
        position: "relative",
        '& button': {
            position: "absolute",
            padding: 0,
            left: 100
        },
    },
    tooltip: {
        position: "absolute",
        padding: 0,
        top: 11,
        right: 0,
        "&>* > span": {
            fontSize: "20px"
        },
    },
    productShirt: {
        display: "flex",
        "& p": {
            paddingLeft: 10,
            fontSize: 16
        }
    },
    headerCont: {
        "& th": {
            fontSize: 17,
        },
    },
    cost: {
        fontSize: 16,
        fontWeight: 600
    },
    priceCont: {
        '& input': {
            paddingRight: "0px !important",
        },
        '& .MuiInputAdornment-positionEnd': {
            marginLeft: 0
        },
        '& svg': {
            fontSize: 20
        },
    },
    profit: {
        fontSize: 16,
        "& span": {
            color: theme.palette.primary.main,
            fontSize: 20,
            fontWeight: 600
        },
    },
    // right Part
    rightPart: {
        marginLeft: 10,
        backgroundColor: "#F1F4F8",
        padding: 50,
        width: 'fit-content',
        minWidth: 600,
        height: 400,
    },
    content: {

    },
    rightHeader: {
        fontWeight: 600,
        fontSize: 18,
        marginTop: 30,
        letterSpacing: 2,
        marginBottom: 10
    },
    firstLine: {
        display: "flex",
        maxWidth: 320,
        alignItems: "center",
        "& img": {
            backgroundColor: "white",
            borderRadius: "100%",
            padding: "20px",
            width: 100,
            height: 100
        },
        "& p": {
            textAlign: "center"
        }
    },
    simpleTxt: {
        fontSize: 15,
        marginLeft: 17,
    },
    totalCost: {
        fontWeight: 900,
        fontSize: 30,
        marginTop: 40,
        marginLeft: 30,
        letterSpacing: 1.5,
    }
}))