import { Box, Tab, Tabs } from "@material-ui/core";
import { images } from "assets/icons/editor/images.ts";
import { ConfirmationDialog } from "components/Dialogs";
import { fabric } from "fabric";
import 'fabric-history';
import { EditorNavbar } from 'pages/EditorPages/components/index.js';
import React, { Component } from 'react';
import Canvas from './canvas.jsx';
import { AddProduct, AddText, ProductModal, SavingModal } from './components';
import Crop from "./components/Crop/Crop.jsx";
import { FirstPayment, SecondPayment } from "./components/PaymentPages/index.js";
import { TxtStuffs } from './components/txtStuffs';
import "./editor.css";

export class Editor extends Component {
    state = {
        'currObjText': '',
        'currFontSize': 30,
        value: 0,
        activeObjCoordinates: {
            height: 0,
            width: 0
        },
        editorStep: 0,
        deleteSelectedObject: false,
        confirmModal: false,
        savingModal: false,
        imageButtonType: 'upload'
    };
    componentDidMount = () => {
        this.objectSelected();
        this?.canvas?.on({ 'object:selected': this.objectSelected, 'selection:updated': this.objectSelected });
        this?.canvas?.on('selection:cleared', this.clearInputs);

        // window.addEventListener('keydown', (e) => {
        //     var selection = this.canvas.getActiveObject();
        //     if (e.key === 'Delete') {
        //         this.setState({ confirmModal: true })
        //         this.canvas.remove(selection);
        //         this.setState({ deleteSelectedObject: false })
        //     }
        // })

    }

    handleTabChange = (event, newValue) => {
        this.setState({ value: newValue })
    };

    a11yProps = (index) => {
        return {
            id: `scrollable-prevent-tab-${index}`,
            'aria-controls': `scrollable-prevent-tabpanel-${index}`,
        };
    }



    render() {
        const TabPanel = (props) => {
            const { children, value, index, ...other } = props;
            return (
                <div
                    role="tabpanel"
                    hidden={value !== index}
                    id={`scrollable-prevent-tabpanel-${index}`}
                    aria-labelledby={`scrollable-prevent-tab-${index}`}
                    {...other}
                >
                    {value === index && (
                        <Box p={3}>
                            {children}
                        </Box>
                    )}
                </div>
            );
        }
        return (
            <>
                <SavingModal
                    handleClose={() => this.setState({ savingModal: false })}
                    open={this.state.savingModal}
                />

                <ConfirmationDialog
                    open={this.state.confirmModal}
                    handleClose={() => this.setState({ confirmModal: false })}
                    title="Seçilən obyekti silmək istəyirsinizmi?"
                    isAccepted={(accept) => accept && this.deleteObject()}
                />
                <EditorNavbar
                    stepCount={(step) => this.setState({ editorStep: step })}
                    step={this.state.editorStep}
                    onFinish={() => this.setState({ savingModal: true })}
                    restoreCanvas={() => this.writeToLocalStorage(document.getElementById('canvas'), document.getElementById('parentCanvas'))}
                />
                {
                    this.state.editorStep === 0 &&
                    <div className="pageContainer">
                        <div>
                            <Canvas
                                canvas={(canvas) => {
                                    this.canvas = canvas
                                }}
                            />
                        </div>
                        <div className="rightPart">
                            <h2 className="createYourDesign">Create your design</h2>
                            <Tabs value={this.state.value} scrollButtons="off" className="tabContainer" onChange={this.handleTabChange}>
                                <Tab className={`tab`} label="Pick products" icon={<img src={images.product} />} {...this.a11yProps(0)} />
                                <Tab className={`tab`} label="Add text" icon={<img src={images.text} />} {...this.a11yProps(1)} />
                                <Tab className={`tab`} label="Find graphics" icon={<img src={images.graphics} />} {...this.a11yProps(2)} />
                                <Tab className={`tab`} label="Upload art" icon={<img src={images.upload} />} {...this.a11yProps(3)} />
                            </Tabs>
                            <TabPanel value={this.state.value} index={0}>
                                <p>Select up to 9 styles to add to your design </p>
                                <ProductModal style={{ marginBottom: 30 }} />

                                <AddProduct style={{ marginTop: 40 }} />
                            </TabPanel>
                            <div
                                role="tabpanel"
                                hidden={this.state.value !== 1}
                                id={`scrollable-prevent-tabpanel-${1}`}
                                aria-labelledby={`scrollable-prevent-tab-${1}`}
                            >
                                {this.state.value === 1 && (
                                    <Box p={3}>
                                        <div className="form-group">
                                            <AddText
                                                onChange={(event) => this.setState({ 'currObjText': event.target.value })}
                                                defaultValue={this.state.currObjText}
                                                onClick={(txt) => this.addText(txt)}
                                            />
                                        </div>
                                        <div className="txtPart">
                                            <TxtStuffs
                                                applyFont={(txt) => this.applyFont(txt)}
                                                styleText={((txt) => this.styleText(txt))}
                                                textColor={(clr) => this.textColor(clr)}
                                                spacing={(value) => this.spacing(value)}
                                                rotate={(value) => this.rotate(value)}
                                                size={(value) => this.fontSize(value)}
                                                moveLayer={(txt) => this.moveLayer(txt)}
                                                flip={(dir) => this.flip(dir)}
                                                _delete={() => {
                                                    if (this.canvas.getActiveObject())
                                                        this.setState({ confirmModal: true })
                                                }}
                                                undo={() => this.canvas.undo()}
                                                redo={() => this.canvas.redo()}
                                                isTxt={true}
                                            />
                                        </div>
                                    </Box>
                                )}
                            </div>
                            <TabPanel value={this.state.value} index={2}>
                                {/* <input type="file" id="imgLoader" onChange={(e) => this.handleImage(e)} /><br></br> */}
                                {/* servedeki image-ler bura gelecek */}
                            </TabPanel>
                            <TabPanel value={this.state.value} index={3}>
                                <div className="imgUploadClass">
                                    <Crop onClick={(data) => this.handleImage(data, this.canvas)} imageButtonType={this.state.imageButtonType} />
                                </div>
                                <div className="txtPart">
                                    <TxtStuffs
                                        rotate={(value) => this.rotate(value)}
                                        moveLayer={(txt) => this.moveLayer(txt)}
                                        flip={(dir) => this.flip(dir)}
                                        _delete={() => {
                                            if (this.canvas.getActiveObject())
                                                this.setState({ confirmModal: true })
                                        }}
                                        size={(value) => this.imgSize(value)}
                                        undo={() => this.canvas.undo()}
                                        redo={() => this.canvas.redo()}
                                        isTxt={false}
                                    />
                                </div>
                            </TabPanel>
                        </div>
                    </div>
                }
                {
                    this.state.editorStep === 1 &&
                    <div className="pageContainer">
                        <FirstPayment />
                    </div>
                }
                {
                    this.state.editorStep === 2 && <SecondPayment />
                }
            </>
        );
    }

    // functions 
    writeToLocalStorage = (canvas, prntCnvs) => {
        // create third canvas to combine previous ones
        var output = document.createElement('canvas');
        var octx = output.getContext('2d');
        // discarded all controls over the active objects
        this.canvas.discardActiveObject().renderAll();
        output.width = 600;
        output.height = 600;
        octx.drawImage(prntCnvs, 0, 0, 600, 600);
        octx.drawImage(canvas, 175, 100, 250, 400);

        octx.save();
        console.log('octx', octx)
        octx.webkitImageSmoothingEnabled = false;
        octx.mozImageSmoothingEnabled = false;
        octx.imageSmoothingEnabled = false;

        let dataUrl = output.toDataURL("image/png", 1.0)
        localStorage.setItem('img', dataUrl);
        const json = JSON.stringify(this.canvas)
        localStorage.setItem('templateOfTShirt', json)

    }


    objectSelected = () => {
        const selectedObj = this?.canvas?.getActiveObject()
        if (selectedObj != null && selectedObj.type === "text") {
            let newText = selectedObj.text;
            let newFSize = selectedObj.fontSize;
            this.setState({ 'currObjText': newText, 'currFontSize': newFSize });
        }
    };

    clearInputs = () => {
        this.setState({ 'currObjText': '', 'currFontSize': 30 });
    };

    addText = (enteredText) => {
        if (enteredText != null && enteredText) {
            let object;
            if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
                object = this.canvas.getActiveObject();
                object.text = enteredText;
            } else {
                object = new fabric.Text(enteredText, {
                    fill: 'black', left: 80, top: 160, fontSize: 30,
                    originX: "center",
                    originY: 'center',
                });
                this.canvas.add(object);
                this.canvas.setActiveObject(object);
            }
            this.canvas.renderAll();
            this.canvas.setActiveObject(object);
        }
    };


    applyFont = (_fontFamily) => {
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
            let activeObj = this.canvas.getActiveObject();
            activeObj.fontFamily = _fontFamily;
            this.canvas.renderAll();
        }
    };

    styleText = (what) => {
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
            let activeObj = this.canvas.getActiveObject();
            switch (what) {
                case 'Bold':
                    activeObj.fontWeight = (activeObj.fontWeight === 'bold') ? '' : 'bold';
                    break;
                case 'Italics':
                    activeObj.fontStyle = (activeObj.fontStyle === 'italic') ? '' : 'italic';
                    break;
                case 'Underline':
                    activeObj.underline = !activeObj.underline;
                    break;
                default:
                    break;
            }
            this.canvas.renderAll();
        }
    };

    fontSize = (value) => {
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
            let activeObj = this.canvas.getActiveObject();
            activeObj.fontSize = value;
            this.canvas.renderAll();
            this.setState({ 'currFontSize': value });
        }
    };
    imgSize = (value) => {
        if (value === 0) {
            value = 1
        }
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "image") {
            let activeObj = this.canvas.getActiveObject();
            let scale = value * 0.0036666666666667
            activeObj.scaleX = scale;
            activeObj.scaleY = scale;
            this.canvas.renderAll();
        }
    };

    textColor = (clr) => {
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
            let activeObj = this.canvas.getActiveObject();
            activeObj.fill = clr;
            this.canvas.renderAll();
        }
    };

    flip = (direction) => {
        if (this.canvas.getActiveObject() != null && (this.canvas.getActiveObject().type === "text" || this.canvas.getActiveObject().type === "image")) {
            this.canvas.getActiveObject().toggle('flip' + direction);
            this.canvas.renderAll();
        }
    };

    spacing = (value) => {
        value = value * 5;
        if (this.canvas.getActiveObject() != null && this.canvas.getActiveObject().type === "text") {
            let activeObj = this.canvas.getActiveObject();
            activeObj.charSpacing = value;
        }
        this.canvas.renderAll()
    }

    moveLayer = (direction) => {
        if (this.canvas.getActiveObject() != null) {
            if (direction === "UP")
                this.canvas.getActiveObject().bringForward();
            else
                this.canvas.getActiveObject().sendBackwards();
            this.canvas.renderAll();
        }
    };

    deleteObject = () => {
        this.setState({ confirmModal: false })
        this.canvas.remove(this.canvas.getActiveObject());
        this.setState({ deleteSelectedObject: false })
    };

    handleImage = (data, canvas) => {
        var img = document.createElement('img');
        img.onload = () => {
            // if (img.width < 1000 || img.height < 1000) {
            //     alert("upload image should be greater then 1000px*1000px");
            //     return;
            // }
            var oImg = new fabric.Image(img);
            var oImg = oImg.set({ left: 50, top: 100, angle: 0 }).scaleToWidth(90);
            canvas.add(oImg);
            canvas.setActiveObject(oImg);
        };
        img.src = data;
        if (img.src) {
            this.state.cr
        }
    }

    rotate = (newValue) => {
        if (this.canvas.getActiveObject() != null) {
            this.canvas.getActiveObject().angle = newValue;
        }
        this.canvas.renderAll();
    };
}

