import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    leftSection: {
        display: "flex",
        width: "50%",
    },
    imgContainer: {
        width: "500px",
        height: 500,
        maxHeight: 500,
        maxWidth: 500,
        overflow: "hidden",
        "&>img": {
            width: 500,
            height: 500,
            "&:hover": {
                transform: "scale(1.8)",
                transformOrigin: "50% 30%",
                transition: "all 100ms  ease"
            }
        }
    },
    smallImgContainer: {
        display: "flex",
        flexDirection: "column",
        justifyContent: 'flex-end',
        marginRight: 20,
        "&>img": {
            padding: 5,
            width: 100,
            height: 100,
            "&:hover": {
                cursor: "pointer"
            }
        }
    },
    rightSection: {

    },
    header: {
        fontWeight: 600,
        textAlign: "center"
    },
    illustration: {
        width: 350,
        fontSize: 20,
        marginTop: 20,
        marginBottom: 40,
        backgroundColor: "#F3F5F9",
        borderRadius: 5,
        padding: "15px 20px",
        position: 'relative',
        "&::after": {
            content: "''",
            width: 0,
            height: 0,
            borderLeft: "15px solid transparent",
            borderRight: "15px solid transparent",
            borderTop: "15px solid #F3F5F9",
            position: "absolute",
            left: "40px",
            bottom: "-15px"
        }

    },
    avatar: {
        width: 20,
        height: 20
    },
    avatarContainer: {
        display: "flex",
        alignItems: "center",
        marginBottom: 30,
        "&>img": {
            marginRight: 10
        },
        "&>p": {
            fontSize: 17
        }
    },
    colorTxt: {
        fontSize: 13,
        marginTop: 10,
        marginBottom: 10,
        letterSpacing: 4
    },
    colorsContainer: {
        display: "flex",
        marginBottom: 40,
        "&>div": {
            "&:hover": {
                cursor: "pointer",
                border: "1px solid #ccc"
            }
        }
    },
    color: {
        width: 30,
        height: 30,
        borderRadius: "100%",
        marginRight: 20,
        display: "flex",
        justifyContent: 'center',
        alignItems: 'center'
    },
    sizeContainer: {
        display: "flex",
        justifyContent: 'space-between',
        "&>p:not(:last-child)": {
            padding: "0px 10px",
            borderRadius: 6,
            border: "1px solid #ccc",
            "&:hover": {
                cursor: "pointer",
                backgroundColor: '#ccc',
                color: "white"
            }
        },
        "&>:last-child": {
            color: "#6582A3",
            "&:hover": {
                cursor: 'pointer'
            }
        }
    },
    btn: {
        padding: "20px 25px",
        marginTop: 20
    },
    title: {
        height: 60,
        width: 300,
        "& .MuiOutlinedInput-root": {
            height: "100%"
        }
    },
    description: {
        height: 50,
        width: 600,
        marginBottom: 20,
        "& .MuiOutlinedInput-root": {
            fontSize: 15,
            height: "100%"
        }
    }
}))