import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => {
    const primary = theme.palette.primary.main;
    const secondary = theme.palette.secondary.main;
    return ({
        navbar: {
            padding: `10px ${theme.spacing(6)}px`,
            display: "flex",
            justifyContent: 'space-between',
            borderBottom: `1.5px solid ${secondary}`,
            backgroundColor: "white"
        },
        logo: {

        },
        stepCont: {
            display: "flex",
            minWidth: 325,
            justifyContent: 'space-between',
            alignItems: "center"
        },
        navbarChild: {

        },
        // components
        step: {
            display: 'flex',
            justifyContent: "flex-start",
            position: 'relative',
            "&::after": {
                content: '""',
                position: "absolute",
                width: "37px",
                top: '-5px',
                right: "-31.3px",
                height: "0px !important",
                borderBottom: `1px solid ${secondary}`,
                transform: "matrix(-0.55, -0.89, 0.77, -0.58, 0, 0)",
            },
            "&::before": {
                content: '""',
                position: "absolute",
                width: "34px",
                bottom: '-7.1px',
                right: "-30.1px",
                height: "0px !important",
                borderBottom: `1px solid ${secondary}`,
                transform: "matrix(0.58, -0.88, 0.74, 0.6, 0, 0)",
            },
            "& p": {
                lineHeight: "unset",
                margin: 0
            }
        },
        step__step: {
            borderRadius: "50%",
            marginRight: "8px !important",
            width: 20,
            height: 20,
            border: `1px solid ${secondary}`,
            textAlign: 'center',
            "&:hover": {
                backgroundColor: primary,
                cursor: "pointer",
                color: "white"
            }
        },
        step__header: {
        },
        button: {
            width: "100px",
            fontSize: "15px",
            fontWeight: 500,
            color: "white",
            backgroundColor: primary,
            "&:hover": {
                backgroundColor: primary,
                marginTop: -3
            }
        },
        account: {
            display: "flex",
            alignItems: "center"
        },
        profile: {
            width: "40px",
            height: "40px",
            marginLeft: 10,
            borderRadius: "50%",
            padding: 2,
            border: `2px solid ${secondary}`,
            marginRight: 10

        }
    })
})