import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    container: {
        display: 'flex',
        justifyContent: "space-between",
        width: 550,
        alignItems: "center",
        padding: "20px 10px",
        border: "1px solid #eee",
        "&:hover": {
            cursor: "pointer",
            backgroundColor: "#eee"
        }
    },
    addProduct: {
        textAlign: "center",
        width: "100%",
        textTransform: "uppercase",
        marginBottom: 0,
        lineHeight: "unset",
        letterSpacing: 3,
        userSelect: "none",
        pointerEvents: "none"

    }

}))